const MapProxy: {
    [key in string]: {
        resolve: Function,
        reject: Function
    }[]
} = {};

function getProxy(path: string): { resolve: Function, reject: Function }[] | null {
    if (Object.keys(MapProxy).indexOf(path) != -1) {
        return MapProxy[path];
    }
    return null;
}
function setProxy(path: string, option: { resolve: Function, reject: Function }) {
    if (Object.keys(MapProxy).indexOf(path) == -1) {
        MapProxy[path] = [];
    }
    MapProxy[path].push(option);
}

/// @ts-ignore
globalThis.requestResponse = function (path: string, data: string) {
    const result = getProxy(path);
    if (result != null) {
        result.forEach(item => {
            item.resolve(JSON.parse(data));
        })
    }
}
// @ts-ignore
globalThis.requestError = function (path: string, message: string) {
    const result = getProxy(path);
    if (result != null) {
        result.forEach(item => {
            item.reject(JSON.parse(message));
        })
    }
}

function request(path: string, json: any) {
    /// @ts-ignore
    if (webObj != undefined) {
        return new Promise((resolve, reject) => {
            setProxy(path, { resolve, reject });
            // @ts-ignore
            webObj.postMessage(JSON.stringify({
                cmd: 'request',
                data: {
                    path: path,
                    json: json
                }
            }))
        })
    }
    return Promise.reject({ code: -1, message: 'Service Error' });
}

export interface AccountInfo {
    Points: {
        currency: string,
        available: string
        hold: string
        withdraw: string
    },
    THB: {
        currency: string,
        available: string
        hold: string
        withdraw: string
    },
    USD: {
        currency: string,
        available: string
        hold: string
        withdraw: string
    }
}
export function getAccount(): Promise<AccountInfo> {
    const path = '/user/v1/getAccount';
    const json = { timestamp: Date.now() };
    return request(path, json) as Promise<AccountInfo>;
}


